.projects {
    padding-bottom: 0 !important;
}

.projects__inner {
    $self: &;

    &__content {
        display: flex;

        #{$self}-item {
            background: #000;

            &:hover {
                #{$self}-img {
                    opacity: 0.2 !important;
                }

                #{$self}-title {
                    opacity: 1 !important;
                }
            }

            .img-wrapper {
                position: relative;

                #{$self}-title {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translateX(-50%);
                    font-family: "Source Sans Pro", sans-serif;
                    font-size: 20px;
                    color: $grey;
                    text-transform: uppercase;
                    letter-spacing: 5px;
                    opacity: 0;
                    transition: opacity 0.4s ease-in-out;
                }

                #{$self}-img {
                    transition: opacity 0.4s ease-in-out;
                }
            }
        }
    }

    &-submit {
        @include submit;
    }
}
